@use './src/styles/utils/variables';

@mixin focus {
  color: variables.$main-bg;
  background: variables.$light-gray;
  border: 1px solid #FFB45C;
  box-shadow: 0px 0px 4px rgba(70, 136, 235, 0.25);
  opacity: 1;
}

select {
  /*check font size*/
  font: normal 400 14px/16px variables.$roboto;
  color: rgba(170, 178, 189, 0.5);
  display: flex;
  align-items: center;
  background: variables.$white;
  border: 1px solid rgba(170, 178, 189, 0.5);
  box-shadow: inset 1px 1px 5px rgba(208, 210, 217, 0.5);
  border-radius: 10px;
  padding: 14px 16px;
  outline: none;

  &:focus {
    @include focus();
  }
}